@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.forHeader {
  background-color: #fafafa;
  margin: 0;
  padding: 0;
}

.whyChoose {
  margin-top: 370px;
}

.HeaderSection {
  max-height: 636px;
}

#forMobilesImg {
  position: relative;
}

.forimg1 {
  top: 0;
  right: 0;
  height: 43.5rem;
  margin-right: -7rem;
}

.container {
  margin: 0 auto;
}

.forimg1 img {
  height: 100%;
}

.lightFont {
  font-family: "Public Sans", sans-serif;
  font-size: 0.9803921569rem;
  font-weight: 300;
}

.card-body {
  line-height: 20px;
}

.footer {
  background-color: #2d314d;
}

.regularFont {
  font-family: "Public Sans", sans-serif;
  font-size: 1.1666666667rem;
  font-weight: 400;
}

.boldFont {
  font-family: "Public Sans", sans-serif;
  font-size: 1.6666666667rem;
  font-weight: 700;
}

.bg-lime {
  background-image: linear-gradient(to right, #31d35c, #2bb7da);
}

.bg-grayesh {
  background-color: #f3f4f6;
}

.my-sm-text {
  font-size: 11px;
}

.footer-link {
  text-decoration: none;
  color: white;
  text-align: center;
}

.card-link {
  text-decoration: none;
  color: black;
  text-align: center;
}

i:hover, a:hover {
  color: #31d35c;
}

#requestBtn:hover {
  background-image: linear-gradient(to right, #a8e6b9, #a3e9fa);
}

.nav-item:hover {
  border-bottom: #31d35c 4px solid;
}

.card {
  font-size: 1.6666666667rem;
}

@media only screen and (min-width: 375px) {
  /* for tablets */
  .forimg1 {
    margin-right: 0;
  }
}
@media only screen and (min-width: 768px) {
  .whyChoose {
    margin-top: 0rem;
  }

  .forimg1 {
    margin-right: -7rem;
  }
}